import React from "react";
import {
    Container,
    Row,
    Col
} from "reactstrap";

import "./CardImg.css";
import CardItem from "./CardItem";

import courseImg01 from "../../assests/images/web-development.png";
import courseImg02 from "../../assests/images/csharp.jpg";
import courseImg03 from "../../assests/images/about-us.png";
import courseImg04 from "../../assests/images/processor.jpg";

const dataimg = [
    {
        id: "01",
        title: "Django + Python",
        imgUrl: courseImg01,
        desc: `Django     -   Python,    MVC.    Django Software Foundation.   Django      ,      `
    },
    {
        id: "02",
        title: ".Net Core (C#)",
        imgUrl: courseImg02,
        desc: `C#  -  .   19982001     Microsoft              Microsoft .NET Framework.     ECMA-334  ISO/IEC 23270`
    },
    {
        id: "03",
        title: "  ()",
        imgUrl: courseImg04,
        desc: `  (;      ; . central processing unit, CPU,     ,   )      ,    ( ),         .      . `
    },
    {
        id: "04",
        title: " ",
        imgUrl: courseImg03,
        desc: `    ,     .     ,    ,      ,      `
    },
];

const CardImg = () => {
    return (
        <section>
            <Container className="cardimg-container">
                <Row>
                    <Col lg="12" className="text-center mb-5">
                        <h2 className="fw-bold">. PYTHON   </h2>
                    </Col>

                    {dataimg.map((item) => (
                        <Col lg="3" md="4" className="mb-4" key={item.id}>
                            <CardItem item={item} />
                        </Col>
                    ))}
                </Row>
            </Container>
        </section>
    );
};

export default CardImg;